import React from 'react';
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import MyPostsContainer from "./MyPosts/MyPostsContainer";
import {Navigate} from "react-router-dom";


const Profile = (props) => {
// debugger

  return (
    <div>
      <ProfileInfo status={props.status} updateStatus={props.updateStatus} profile={ props.profile } saveProfile={props.saveProfile}
                   router={props.router} userId={props.userId} isOwner={props.isOwner} savePhoto={props.savePhoto} />
      <MyPostsContainer />
    </div>
  )
}

export default Profile;