import React from 'react';
import Profile from "./Profile";
import {connect} from "react-redux";
import {getUserProfileHeader, getStatus, updateStatus, savePhoto, saveProfile} from "../../redax/profile-reducer";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import {withAuthRedirect} from "../../HOC/withAuthRedirect";
import {compose} from "redux";


class ProfileContainer extends React.Component {

    refreshProfile(){
        let userId = this.props.router.params.userId;
        if(!userId) {
            userId = this.props.authorizedUserId
            if(!userId){
                this.props.history.push("/login")
            }
        }
        this.props.getUserProfileHeader(userId)
        this.props.getStatus(userId)
    }

    componentDidMount() {
        this.refreshProfile()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(this.props.router.params.userId != prevProps.router.params.userId){
            this.refreshProfile()
        }
    }

    render () {
      return (
      <Profile {...this.props} status={this.props.status} profile={ this.props.profile } router={ this.props.router}
               isOwner={!this.props.router.params.userId} userId={ this.props.router.params.userId}
               updateStatus={this.props.updateStatus} savePhoto={this.props.savePhoto} saveProfile={this.props.saveProfile}/>
      )
  }
}

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedUserId: state.auth.userId,
    isAuth: state.auth.isAuth
})

function withRouter(Component) {
    function ComponentWithRouterProp(props) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }
    return ComponentWithRouterProp;
}

export default compose(
    connect ( mapStateToProps, { getUserProfileHeader, getStatus, updateStatus, savePhoto, saveProfile } ),
    withRouter,
    withAuthRedirect
)(ProfileContainer)



