import React, {useEffect, useState} from "react";
import s from './ProfileInfo.module.css'
import {connect} from "react-redux";
import {useLocation, useNavigate, useParams} from "react-router-dom";

const ProfileStatusWithHooks = (props) => {

    let [editeMode, setEditeMode] = useState(false)
    let [status, setStatus] = useState(props.status)

    useEffect(() => {
        setStatus(props.status)
    }, [props.status])

    const doubleClick = () => {
        setEditeMode(  true)
    }

    const blur = () => {
        setEditeMode( false)
        props.updateStatus(status)
    }

    const onStatusChang = (e) => {
        setStatus( e.currentTarget.value)
    }

        return (
            <div>
                { !editeMode &&
                    <div>
                        <b>My dumi:</b> <span onDoubleClick={ doubleClick }> {props.status || '-no status-'} </span>
                    </div>
                }
                { editeMode &&
                    <div>
                        <input onChange={onStatusChang} autoFocus={true} value={status || '--no status--'} onBlur={ blur } />
                    </div>
                }
            </div>
        )
}

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
})

function withRouter(Component) {
    function ComponentWithRouterProp(props) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }
    return ComponentWithRouterProp;
}

let withRouters = withRouter(ProfileStatusWithHooks)

export default connect ( mapStateToProps, {  } ) (withRouters)