import React from "react";
import {Contacts} from "./ProfileInfo";
import {Input, Textarea} from "../../common/FormsControls/FormsControls";
import s from './ProfileInfo.module.css'
import {Field, reduxForm} from "redux-form";

export const ProfileDataForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            {
                props.error && <div className={s.formSummaryError}>
                    {props.error}
                </div>
            }
            <p><b>Full name</b>:</p> <Field name={'fullName'} type="text" placeholder={'Full name'} component={Input} className={s.inp}/>
            <p><b>Looking for a job</b>:</p>
            {<Field name={'LookingForAJob'} type="checkbox" component={Input} className={s.inp}/>}
            <p><b>My professional skills</b>:</p>
            {<Field name={'LookingForAJobDescription'} component={Textarea} className={s.inp} placeholder={'My professional skills...'} />}
            <p><b>About me</b>:</p>
            {<Field name={'aboutMe'} component={Textarea} className={s.inp} placeholder={'About me...'} />}
            <div>
                <b>Contacts</b>: {Object.keys(props.profile.contacts).map(key => {
                    return <div key={key} className={s.contacts}>
                        <b>{key}</b> { <Field name={'contacts.' + key} component={Input} className={s.inp} placeholder={key} /> }
                    </div>
            })}
            </div>
            { <div><button>Save</button></div> }
        </form>
    )
}

export const ProfileReduxForm = reduxForm({ form: 'profile' }) (ProfileDataForm)