import React from 'react';
import s from './MyPosts.module.css'
import Post from "./Post/Post";
import {Field, reduxForm} from "redux-form";
import {maxSymbol20, required} from "../../../utils/validators";
import {Textarea} from "../../common/FormsControls/FormsControls";

const maxSymbolValidate = maxSymbol20(20)

const MyPosts = (props) => {
    // shouldComponentUpdate, PureComponent, memo

    let postsElements =
        [...props.posts]
            .reverse()
            .map( p => <Post key={p.id} massage={p.message} likeCount={p.likeCount} /> )

    const addNewMes = (value) => {
        props.addPostDispatch(value.newPostText)
    }
    return (
        <div className={s.myPost}>
            <h2>My post</h2>
            <div className={s.area}>
                <PostReduxForm onSubmit={addNewMes} />
            </div>
            <div>
                New post
                { postsElements }
            </div>
        </div>

  )
}

const PostForm = (props) => {
    return <div>
        <form className={s.area} onSubmit={props.handleSubmit}>
            <Field validate={[required, maxSymbolValidate ]} component={Textarea} name='newPostText' />
            <div>
                <button>Add post</button>
                <button>Remove</button>
            </div>
        </form>
    </div>
}

const PostReduxForm = reduxForm({ form: 'post' }) (PostForm)

export default MyPosts;



